import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import {QuotesProvider} from '../../providers/quotes/quotes';

import{Camera, CameraOptions } from '@ionic-native/camera';
import {FileTransfer} from '@ionic-native/file-transfer';

/**
 * Generated class for the CreateQuotePage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-create-quote',
  templateUrl: 'create-quote.html',
})
export class CreateQuotePage {
  content;
  author;

  constructor(public navCtrl: NavController, public navParams: NavParams, private quotesProvider: QuotesProvider
   , public camera:Camera,  public fileTransfer:FileTransfer) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad CreateQuotePage');
  }

  onAddQuote(){
    this.quotesProvider.postQuote(this.content, this.author).subscribe(data => {
      console.log(data);
    });
  }

imgData;
imgUrl;
hideImage=true;

  getPhoto(){
let options:CameraOptions={quality:50, destinationType:this.camera.DestinationType.DATA_URL,
sourceType:this.camera.PictureSourceType.CAMERA,
allowEdit:false,
encodingType:this.camera.EncodingType.PNG,
correctOrientation:true,
targetHeight:512,
targetWidth:512
}
this.camera.getPicture(options).then((ImageData)=>{
  this.imgData=ImageData;
  this.imgUrl="data:image/png;base64,"+ImageData;
  this.hideImage=false;
})
  }

  uploadPhoto(){
    let t=this.fileTransfer.create();
    t.upload(this.imgUrl,"http://fasomatin.com/wp-json/wp/v2/media",{
      headers:{
"Authorization":"Basic " +btoa("username:password"),
"content-disposition":"attachement; filename=\'samarthadavod.png\'"
      }
    } ).then((res)=>{
      alert(JSON.stringify(res))
    }).catch((err)=>{
      alert(JSON.stringify(err));
    })
  }

}



